ul {
  list-style: none;
}
.movie {
  display: flex;
  flex-direction: column;
  width: 500px;
  height: 270px;
  background-color: #ffffff;
  margin: 25px;
  box-shadow: 0 10px 10px rgba(0, 0, 0, 0.1);
}
.upper {
  display: flex;
  padding: 20px;
  height: 180px;
}
.movie__poster img {
  height: 210px;
  position: relative;
  top: -30px;
  box-shadow: 0.3px 10px 15px 3px rgba(194, 196, 199, 0.6);
}
.movie__data {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 0 20px;
  overflow: hidden;
}
.movie__year,
.genres__genre,
.rating > span {
  font-size: 1.2rem;
  line-height: 2rem;
  font-weight: 500;
  color: #dee0ec;
}
.movie__title-year {
  display: flex;
  flex-wrap: wrap;
  align-items: baseline;
  width: 100%;
  padding-top: 6px;
}
.movie__title {
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  color: #48485b;
  font-size: 1.5rem;
  font-weight: 600;
  margin-right: 10px;
  word-break: break-word;
}
.movie__year {
  margin-right: auto;
  color: #555555;
}
.more {
  margin-left: auto;
}

.genres__genre {
  float: left;
  line-height: 1rem;
  padding-top: 0.6rem;
}
.genres__genre::after {
  content: ",\a0";
}
.genres__genre:last-child::after {
  content: "";
}
.rating-div {
  padding: 5px 0;
}
.rating-div > span {
  font-weight: 300;
}
.movie__summary {
  word-break: break-word;
  line-height: 1.7rem;
  font-size: 1.4rem;
  font-weight: 400;
  letter-spacing: 0;
  color: #c7c9d8;
}
.movie__summary > svg {
  display: none;
  vertical-align: middle;
}
.below {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 50px;
  margin: 0 20px;
  border-top: 1px solid #eeeeee;
}
.recommendation * {
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  font-size: 1.2rem;
  line-height: 2rem;
  font-weight: 500;
  color: #c7c9d8;
  vertical-align: middle;
}
.friends {
  display: inline-flex;
  flex-direction: row-reverse;
  margin-right: 4px;
}
.friend-pf {
  width: 1.2rem;
  height: 1.2rem;
  border-radius: 50%;
  border: 0.7px solid #ffffff;
  object-fit: cover;
}
.friend-pf:not(:last-child) {
  margin-left: -3px;
}
