html {
  font-size: 14px;
}
* {
  margin: 0;
  padding: 0;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS" sans-serif;
  color: #b1b1b3;
  font-weight: 300;
}
.load-screen {
  background-color: #ffffff;
  width: 100vw;
  height: 100vh;
}
.loader {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}
.loader__img {
  width: 10rem;
  height: 10rem;
}
ul {
  list-style: none;
}
body {
  height: 100vh;
  margin: 0;
  background: linear-gradient(135deg, #dfe7ff, #bafaff);
}
.background {
  position: absolute;
  margin: 0;
  width: 100%;
  min-height: 100%;
  display: flex;
  justify-content: center;
}
.container {
  position: absolute;
  display: flex;
  background-color: #eff3f7;
  width: 95%;
  max-height: 100%;
  height: initial;
  top: 50px;
  box-shadow: 0 50px 30px 4px rgba(0, 0, 0, 0.2);
}

.nav {
  position: relative;
  min-width: 260px;
  display: flex;
  flex-direction: column;
  background-color: white;
  align-items: center;
  box-shadow: 4px 0 30px 0 rgba(0, 0, 0, 0.05);
}
.nav-top {
  display: flex;
  flex-direction: column;
  min-height: 300px;
  align-items: center;
}
.word-mark-div {
  display: flex;
  width: 100%;
  height: 100px;
  text-align: center;
  margin: auto auto 0;
}
.word-mark {
  width: 100%;
  margin: auto auto 0;
}
.wm.watch {
  font-family: sans-serif;
  display: inline;
  font-size: 2.2rem;
  font-weight: 500;
  color: #626480;
}
.wm.movies {
  font-family: sans-serif;
  display: inline;
  font-size: 2.2rem;
  font-weight: 300;
  color: #87899e;
}
.profile {
  display: flex;
  flex-direction: column;
  height: 15rem;
  align-items: center;
}
.profile-img-div {
  padding: 3rem 0 1.6rem;
}
.profile-img {
  border-radius: 50%;
  width: 6.4rem;
  height: 6.4rem;
  object-fit: cover;
  box-shadow: 0 4px 12px 0 rgba(0, 0, 0, 0.3);
}
.id {
  font-family: sans-serif;
  font-size: 1.3rem;
  font-weight: 600;
  color: #bbc0d3;
}

/*메뉴*/
.menu {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: auto;
  align-items: flex-start;
}
.menu * {
  color: #d2d6df;
  font-size: 1.3rem;
  font-weight: 600;
  font-family: sans-serif;
}
.main__txt,
.labels__txt {
  display: inline-block;
  height: 40px;
  font-size: 1.2rem;
  padding: 0 40px;
  display: flex;
  align-items: center;
}
.main.svg-icon,
.label.svg-icon {
  height: auto;
  padding-right: 1.3rem;
}
.main.svg-icon {
  width: 1.8rem;
}
.label.svg-icon {
  width: 1.4rem;
}
.main-tab,
.labels-tab {
  width: 100%;
  height: auto;
  padding: 20px 0;
}

.line-div {
  width: 65%;
  padding: 10px 40px;
}
.line {
  height: 2px;
  border-bottom: 3px solid #eff3f7;
}

.main-ul,
.labels-ul {
  height: auto;
  display: flex;
  flex-direction: column;
}
.navli {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  padding: 15px 40px;
}
.navli:hover .main.svg-icon * {
  stroke: #59485d;
}
.navli:hover {
  border-left: 3px solid #13e3f7;
  background: linear-gradient(
    to right,
    rgba(35, 229, 248, 0.1) 0% 1.5%,
    rgba(0, 0, 0, 0) 5% 100%
  );
  color: #59485d;
}

/*메인 컨테이너*/
.main-container {
  width: 100%;
  position: relative;
  margin: 0px 100px 50px;
  padding-top: 120px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.movies {
  width: 100%;
  height: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  overflow: scroll;
}

/**스크롤바**/
.movies::-webkit-scrollbar-track,
.movies::-webkit-scrollbar-track-piece {
  background: #f2f5f7;
}

/* Handle */
.movies::-webkit-scrollbar-thumb {
  background: #d5dde9;
  height: 1px;
}
/* Handle on hover */
.movies::-webkit-scrollbar-thumb:hover {
  background: #bfc8d6;
  height: 1px;
}

.nav-container {
  position: absolute;
  top: 0;
  display: flex;
  justify-content: center;
  width: 100%;
}
.search-filter {
  display: flex;
  height: 120px;
  width: 100%;
  justify-content: space-between;
  background-color: #eff3f7;
  margin: 0 50px;
  align-items: center;
}
input,
select {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  border: none;
  color: #bfc7df;
  background-color: #eff3f7;
  font-size: 1.4rem;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  font-weight: 400;
}
input:focus,
select:focus {
  outline: none;
}
.search-input {
  width: 120px;
}
.search-input::-webkit-input-placeholder {
  color: #bfc7df;
}
.search-label {
  display: flex;
  padding-bottom: 0.4rem;
  border-bottom: 1px solid #dee4f0;
}
.search.svg-icon {
  display: inline-block;
  width: 1.4rem;
  height: 1.4rem;
  margin-right: 0.6rem;
}
.filter.svg-icon {
  vertical-align: middle;
  padding-left: 5px;
}
